import React, { Component } from 'react';
import { Card, Button, Row, Col, Form } from 'react-bootstrap';
import UploadBox from '../../CommonComponents/Uploads/UploadBox';
import { customPostsubmit } from '../../../../../actions/appsMoreAction';
import { GET_ADD_FEED, getModeration } from '../../../../../actions/moderationActions';
import { formatTemplateText, TOAST_ERROR, TOAST_SUCCESS } from '../../../../../utils';
import { connect } from 'react-redux';
import withRouter from '../../../../routes/WithRouter';
import { TAGSHOP_USER_HOME_DATA } from '../../../../../actions/sceneActions';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { getUserInfo_afterChanges, SkipOnBoarding } from '../../../../../actions/loginActions';
import AttachImage from '../../CommonComponents/Uploads/AttachImage';
import Select from "react-select";
class ManualUploadModal extends Component {
       state = {
              postImage: '',
              mediaType: '',
              height: 0,
              width: 0,
              thumbnail: '',
              spinner: false,
              content: "",
              contentError: false,
              authorImage: '',
              author: '',
              networkId: ''
       }
       appContent = window?.APP_CONTENT ?? {};

       componentDidMount() {
              const { networkList, getNetworks } = this.props
              if (!networkList) getNetworks()
       }
       onChangePostImg = value => {
              this.setState({ postImage: value })
       }

       componentDidUpdate(prevProps) {
              const { editData } = this.props
              if (prevProps.editData != editData) {
                     this.setState({ postImage: editData.postFile, height: editData.mediaHeight, width: editData.mediaWidth, mediaType: editData.type })
              }
       }

       navigateModeration = () => {
              const { updateUserInfo, navigate } = this.props
              SkipOnBoarding({ stepstype: "onboardingskip" }).then(res => {
                     updateUserInfo(() => { navigate(`/content/moderation/${localStorage.getItem('wallId')}`) })
              }
              )
       }

       updateData = (data) => {
              if (data.type == "video") {
                     this.setState({ mediaType: data.type, height: data.height, width: data.width, postImage: data.s3Url, thumbnail: data.s3ThumbUrl })
              }
              else {
                     this.setState({ mediaType: data.type, height: data.meta.height, width: data.meta.width })
              }
       }

       uploadPost = (data) => {

              const { userData, navigate, onBoarding, onNext } = this.props
              const { content } = this.state
              const { author, networkId, authorImage } = data
              let type = 1;
              this.setState({ spinner: true });
              if (data.mediaType == 'content' || data.mediaType == 1) {
                     type = 1;
              } else if ((data.mediaType == 'image' || data.mediaType == 4) && content) {
                     type = 4;
              } else if ((data.mediaType == 'image' || data.mediaType == 2) && !content) {
                     type = 2;
              } else if ((data.mediaType == 'video' || data.mediaType == 3) && !content) {
                     type = 3;
              } else if ((data.mediaType == 'video' || data.mediaType == 5) && content) {
                     type = 5;
              }

              const payload = {
                     postFile: data.mediaType == 'video' ? data.thumbnail : data.postImage,
                     mediaFile: data.mediaType == 'video' ? data.postImage : "",
                     content: String(content).trim(),
                     mediaHeight: data.height ? data.height : 0,
                     mediaWidth: data.width ? data.width : 0,
                     type: type,
                     wallId: localStorage.getItem('wallId'),
                     author, networkId, authorImage

              }

              if (String(content).trim().length > 0 && String(content).trim().length <= 500) {
                     customPostsubmit(payload).then((response) => {
                            if (response.data.responseCode == 200) {
                                   if (response.data.responseStatus == 'success') {
                                          if (!onBoarding) {
                                                 this.props.getModeration()
                                                 this.props.LoadHome()
                                          }
                                          TOAST_SUCCESS(this.appContent?.manual_upload?.post_created ?? "");
                                          onBoarding && window.self == window.top ? onNext() : this.navigateModeration()
                                   } else {
                                          TOAST_ERROR(this.appContent?.common?.some_error_occured ?? "");
                                   }
                            } else if (response.data.responseCode == 409) {
                                   TOAST_ERROR(this.appContent?.manual_upload?.post_exist ?? "");
                            }
                            this.setState({ spinner: false });
                     }).catch((error) => {
                            TOAST_ERROR(error.message);
                            this.setState({ spinner: false })

                     })
              }
              else this.setState({ spinner: false, contentError: true })

       }

       changeContent = (event) => this.setState({ content: (event.target.value).substring(0, 500), contentError: false })

       render() {
              const { navigate, onBoarding, setNetwork, networkList } = this.props
              const { postImage, thumbnail, contentError, content, spinner, authorImage, author, networkId } = this.state
              const netWork = (networkList ?? []).filter(({ id }) => ![0, 45, 46].includes(id)).map(({ id, name }) => ({ label: name, value: id }));

              return (<>
                     <Card.Body className={`${onBoarding ? 'p-0' : ''}`} style={{ minHeight: 400 }}>
                            <Row>
                                   <Col md={6} lg={5} style={{ minHeight: 400 }}>
                                          <UploadBox id="su_logo" backgroundImage={postImage} onClickTouploadImage={this.onChangePostImg} removeDelete={false} icon={'image-regular'} updateData={this.updateData} thumbnail={thumbnail} isEdit={true} />
                                   </Col>
                                   {/* this.updateSocialBanner('logo', 'url', null, url) } */}
                                   <Col md={6} lg={7}>
                                          <div className="mt-4">
                                                 <Form.Label className="mb-2" htmlFor="s_pi">{this.appContent?.manual_upload?.label_author_image ?? ""}</Form.Label>
                                                 <AttachImage id="s_pi" imageStyle={{ paddingBottom: '66%' }} backgroundImage={authorImage} onClickTouploadImage={(url) => this.setState({ authorImage: url })} />
                                          </div>
                                          <Form.Group className='mb-3'>
                                                 <Form.Label className="mb-2" htmlFor="author_n_">{this.appContent?.manual_upload?.label_author_name ?? ""}</Form.Label>
                                                 <Form.Control id="author_n_" name="author_n_" placeholder={this.appContent?.manual_upload?.placeholder_author_name ?? ""} value={author} onChange={(e) => this.setState({ author: e.target.value })} />
                                          </Form.Group>
                                          <div className="mt-2">
                                                 <Form.Label className="mb-2" htmlFor={`s_ntwrk`}>{this.appContent?.manual_upload?.label_select_network ?? ""}</Form.Label>
                                                 <Select id="s_ntwrk" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={netWork}
                                                        value={networkList?.length && networkId ? networkList[networkList?.findIndex(i => i.value == networkId)] : null}
                                                        onChange={e => this.setState({ networkId: e?.value })} />
                                          </div>
                                          <Form.Group className="mt-2 mb-2 mb-xl-3">
                                                 <Form.Label>{this.appContent?.manual_upload?.label_caption ?? ""} <span className="text-danger ms-1">*</span> </Form.Label>
                                                 <Form.Control as='textarea' id="mu_caption" maxLength={500} className={`${contentError ? "is-invalid" : ""} textarea-min-row-5`} name="caption" row="5" value={content} onChange={this.changeContent} />
                                                 {contentError ? <span className='invalid-feedback'>{String(content).length > 0 ? this.appContent?.manual_upload?.error_caption_limit ?? "" : this.appContent?.common?.field_req ?? ""}</span> : null}
                                                 <div className="d-flex jsutify-content-end"> <span className="fs-10 text-muted pt-1">{formatTemplateText(this.appContent?.manual_upload?.caption_char_count ?? "", {
                                                        count: content?.length,
                                                 })}</span> </div>
                                          </Form.Group>

                                   </Col>
                            </Row>
                     </Card.Body>

                     <Card.Footer className={onBoarding ? 'p-0 border-0' : 'px-lg-8'}>
                            <div className='d-flex justify-content-between flex-wrap'>
                                   <div className={`d-flex w-100 align-items-center p-2 ${onBoarding ? ' justify-content-between' : ' justify-content-end'}`}>
                                          <Button variant={onBoarding ? 'link' : 'secondary'} onClick={() => { onBoarding ? setNetwork() : navigate(`/manual-upload`) }}>
                                                 {onBoarding ? <FontAwesomeIcon icon={'arrow-left'} iconClass={'me-2'} /> : ''}
                                                 {this.appContent?.common?.btn_back ?? ""}</Button>
                                          <Button id="create_feed" className={`ms-3 ${spinner ? 'spinner' : ''} `} onClick={() => this.uploadPost(this.state)}>{this.appContent?.common?.upload ?? ""}</Button>
                                   </div>
                            </div>
                     </Card.Footer>
              </>
              );
       }
}

const mapStateToProps = (state) => {
       return {
              userData: state.authorized?.userData?.user_data?.user,
              networkList: state?.addFeedReducer?.addFeedNetworks?.networks
       }
}

const mapDispatchToProps = dispatch => {
       return {
              getModeration: (callback) => dispatch(getModeration(callback)),
              LoadHome: () => dispatch(TAGSHOP_USER_HOME_DATA()),
              updateUserInfo: (callBack) => dispatch(getUserInfo_afterChanges(callBack)),
              getNetworks: () => dispatch(GET_ADD_FEED()),
       }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(ManualUploadModal));