import React, { Component } from 'react';
import { Container, Card, Button, Form, Image, Row, Col, InputGroup, Alert, FormGroup } from 'react-bootstrap'
import { FontAwesomeIcon, baseUrl } from '../../../../../Assets/AssetsHelper';
import { formatTemplateText, GET_NETWORK_ICON, GET_NETWORK_NAME, isValidUrl, parseNumber, TOAST_ERROR, TOAST_SUCCESS } from '../../../../../utils';
import queryString from 'query-string';
import ManualUploadModal from './ManualUploadModal';
import { customPostUrlsubmit } from '../../../../../actions/appsMoreAction';
import withRouter from '../../../../routes/WithRouter';

class ManualUploadWrap extends Component {
       constructor(props) {
              super(props);
              this.state = {
                     spinner:false,
                     network:'',
                     showManual:false,
                     urlError:false,
                     url:''
              };
       }
       appContent = window?.APP_CONTENT ?? {};

       componentDidMount(){
              const {onBoarding, manualSelect} = this.props
              if(onBoarding){
                     this.setState({network:manualSelect, showManual: manualSelect == 0 ? true : false})
              }
              else{
                     const parsed = queryString.parse(window.location.search)
                     if(parsed) this.setState({network:parsed.network, showManual: parsed.network == '0' ? true : false})
              }      
       }

       onSubmit = () => {
              const { url, network } = this.state
              const { onBoarding, onNext } = this.props
              const regex = /^(http:\/\/|https:\/\/).+/;
              const pattern = /instagram\.com\/(?:p|reel)\/([^/?]+)/;
              let checkUrl = false
              if (url.includes('instagram.com')) checkUrl = url.match(pattern);
              else if (url.includes('tiktok.com')) checkUrl = isValidUrl(url);

              if (url && checkUrl) {
                     this.setState({ spinner: true });
                     const data = {
                            networkId: parseInt(network),
                            postUrl: !regex.test(url) ? `https://${url}` : url,
                            wallId: localStorage.getItem('wallId')
                     }

                     customPostUrlsubmit(data).then((response) => {

                            if (response.data.responseCode == 200) {
                                   if (response.data.responseStatus == 'success') {

                                          TOAST_SUCCESS(this.appContent?.manual_upload?.post_created ?? "");
                                          onBoarding ? onNext() : this.props.navigate(`/content/moderation/${localStorage.getItem('wallId')}`)
                                   } else {
                                          TOAST_ERROR(this.appContent?.common?.some_error_occured ?? "");
                                   }
                            }
                            this.setState({ spinner: false });
                     }).catch((error) => {
                            const { responseMessage } = error.response.data
                            TOAST_ERROR(responseMessage);
                            this.setState({ spinner: false });
                     })
              }
              else {
                     this.setState({ urlError: true })
              }
       }


       render() {
              const { spinner, network, showManual, urlError } = this.state
              const {onBoarding, navigate, setNetwork, onNext} = this.props;
              const networkName = GET_NETWORK_NAME(parseNumber(network));
              return (<div className='d-flex flex-column flex-column-fluid w-100 py-5 manual_upload_wrap'>
                            {/* {!onBoarding ?
                            <div className="subheader">
                                   <Container fluid>
                                          <div className='f-between'>
                                                 <h1 className="me-2 h4 my-3">Feeds</h1>
                                          </div>
                                   </Container>
                            </div> : null} */}

                            <Container>
                                   <Row className='f-center manual_upload_card'>
                                          <Col lg={8} xl={6}>
                                                 <Card className={`${onBoarding?'shadow-none border-0':''}`}>
                                                        {onBoarding ? 
                                                        <div className="d-flex align-items-center mb-5">
                                                               <div className="symbol symbol-40 flex-shrink-0 me-3">
                                                                      <Image height={40} width={40} src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(network ? parseInt(network) : 0)}.svg`)} alt={''} fluid />
                                                               </div>
                                                               <div className="d-flex flex-column flex-grow-1 my-lg-0 my-2 pr-3">
                                                                      <span className="fs-6 fw-bold text-gray-800">{networkName}</span>
                                                                      <p className="fs-9 mb-0 fw-medium">{formatTemplateText(this.appContent?.manual_upload?.header_add_post ?? "", { network_name: networkName})}</p>
                                                               </div>
                                                        </div>
                                                        :
                                                        <Card.Header className={`${onBoarding?'border-0':''}`}>
                                                               <div className="d-flex flex-wrap align-items-center py-4">
                                                                      <div className="symbol symbol-50 flex-shrink-0 me-4">
                                                                             <Image src={baseUrl(`media/icons/social-icons-square/${network == '0' ? 'manual-upload' : GET_NETWORK_ICON(network ? parseInt(network) : 0)}.svg`)} fluid />
                                                                      </div>
                                                                      <div className="d-flex flex-column flex-grow-1 my-lg-0 my-2 pe-3">
                                                                             <h3 className="mb-1">{networkName}</h3>
                                                                             <p className="text-muted mb-0 fw-medium">{formatTemplateText(this.appContent?.manual_upload?.header_add_post ?? "", {network_name: networkName})}</p>
                                                                      </div>
                                                               </div>
                                                        </Card.Header>
                                                        }
                                                        {!showManual ? <>
                                                        <Card.Body className={`${onBoarding ? 'p-0' : 'p-lg-8'}`}>
                                                               <div className='my-4'>
                                                                      <FormGroup className='mb-4'>
                                                                             <InputGroup className={`${urlError ? 'is-invalid' : ''}`}>
                                                                             <InputGroup.Text id="basic-addon2"><FontAwesomeIcon type={'solid'} icon={'link'} /></InputGroup.Text>
                                                                                    <Form.Control size='lg' placeholder={this.appContent?.manual_upload?.input_ph ?? ""} onChange={e=>this.setState({url:e.target.value, urlError:false})}/>
                                                                             </InputGroup>
                                                                             {urlError && <span className='invalid-feedback'>{this.appContent?.common?.valid_url ?? ""}</span>}
                                                                      </FormGroup>
                                                                      <Alert variant="warning" className='border border-warning'>
                                                                             <Alert.Heading className='f-center alert_icon_head'>
                                                                                    <span className='f-center'>
                                                                                    <FontAwesomeIcon icon={'lightbulb'} iconClass={'me-1'} />
                                                                                    {this.appContent?.manual_upload?.alert_title_pro_tip ?? ""}
                                                                                    </span>
                                                                             </Alert.Heading>
                                                                             <p className="fw-medium mb-0 fs-8 text-gray-700">{this.appContent?.manual_upload?.alert_desc_pro_tip ?? ""}</p>
                                                                      </Alert>
                                                               </div>
                                                        </Card.Body>
                                                        <Card.Footer className={onBoarding ? 'p-0 border-0':'px-lg-8'}>
                                                               <div className={`d-flex w-100 align-items-center p-2 ${onBoarding?' justify-content-between':' justify-content-end'}`}>
                                                                      <Button variant={onBoarding?'link':'secondary'} onClick={() => onBoarding ? setNetwork() : navigate(`/manual-upload`)}>
                                                                      {onBoarding ? <FontAwesomeIcon icon={'arrow-left'} iconClass={'me-2'} />:''}{this.appContent?.manual_upload?.btn_back ?? ""}</Button>
                                                                      <Button id="create_feed" className={`ms-3 ${spinner ? 'spinner' : ''} `} onClick={this.onSubmit}>{this.appContent?.manual_upload?.btn_import ?? ""}</Button>
                                                               </div>
                                                        </Card.Footer>
                                                        </> : <ManualUploadModal onBoarding={onBoarding} setNetwork={setNetwork} onNext={onNext}/>}
                                                 </Card>
                                          </Col>
                                   </Row>
                            </Container>
                     </div>
              );
       }
}

export default withRouter(ManualUploadWrap);