import React, { Component } from 'react';
import $ from 'jquery';
import { Uppy } from "@uppy/core";
import '@uppy/core/dist/style.min.css';
import '@uppy/dashboard/dist/style.min.css';
import '@uppy/drag-drop/dist/style.min.css';
import '@uppy/status-bar/dist/style.min.css';
import '@uppy/webcam/dist/style.min.css';
import Transloadit from "@uppy/transloadit";
import GoogleDrive from '@uppy/google-drive';
import Url from '@uppy/url';
import { DashboardModal } from '@uppy/react';
import { OverlayTrigger, Tooltip, Button } from 'react-bootstrap';
import { VIDEO_COMPRESS } from '../../../../../actions/api';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { TEMP_URL, UPPY_KEY, UPPY_ROBOT, UPPY_BUCKET, UPPY_REGION, UPPY_AWS_KEY, UPPY_AWS_SECRET_KEY, UPPY_COMPANION_URL, TEMP_FOLDER } from '../../../../../actions/constants';
import axios from "axios";
import VideoType from '../MediaType/VideoType';
import ImageType from '../MediaType/ImageType';

export default class UploadBox extends Component {
       constructor(props) {
              super(props)
              this.uppy = new Uppy({ debug: true, autoProceed: false, restrictions: { maxFileSize: 100000000, maxNumberOfFiles: 1, minNumberOfFiles: 1, allowedFileTypes: ['image/*', 'video/*'] }, onBeforeUpload: this.onBeforeUpload })
                     .use(Transloadit, {
                            target: '#a',
                            params: {
                                   auth: {
                                          key: UPPY_KEY
                                   },
                                   steps: {
                                          store: {
                                                 robot: UPPY_ROBOT,
                                                 bucket_region: UPPY_REGION,
                                                 bucket: UPPY_BUCKET,
                                                 key: UPPY_AWS_KEY,
                                                 secret: UPPY_AWS_SECRET_KEY,
                                                 path: TEMP_FOLDER + '${file.id}.${file.ext}'
                                          }
                                   }
                            },
                            waitForEncoding: true
                     })
                     .use(GoogleDrive, { target: Uppy.Dashboard, companionUrl: UPPY_COMPANION_URL })
                     .use(Url, { target: Uppy.Dashboard, companionUrl: UPPY_COMPANION_URL })
       }

       state = {
              backgroundModal: false, type: null, height: '', width: '', thumbnail: ''
       }

       onBeforeUpload = (files) => {
              const fileKeys = Object.keys(files);
              const { updateData } = this.props
              if (files[fileKeys[0]].meta.type.includes("image")) {
                     this.remove_StatusBar();
              }
              else if (fileKeys && fileKeys.length > 0 && files[fileKeys[0]] && files[fileKeys[0]].meta && files[fileKeys[0]].meta.type.includes("video")) {
                     try {
                            let progressindicators = document.querySelector(".uppy-Dashboard-progressindicators");
                            let html_to_insert = '<div class="uppy-StatusBar is-postprocessing"><div class="uppy-StatusBar-progress is-indeterminate" role="progressbar" aria-valuemin="0" aria-valuemax="100" aria-valuenow="100" style="width: 100%;"></div><div class="uppy-StatusBar-content"><svg class="uppy-StatusBar-spinner" aria-hidden="true" focusable="false" width="14" height="14"><path d="M13.983 6.547c-.12-2.509-1.64-4.893-3.939-5.936-2.48-1.127-5.488-.656-7.556 1.094C.524 3.367-.398 6.048.162 8.562c.556 2.495 2.46 4.52 4.94 5.183 2.932.784 5.61-.602 7.256-3.015-1.493 1.993-3.745 3.309-6.298 2.868-2.514-.434-4.578-2.349-5.153-4.84a6.226 6.226 0 0 1 2.98-6.778C6.34.586 9.74 1.1 11.373 3.493c.407.596.693 1.282.842 1.988.127.598.073 1.197.161 1.794.078.525.543 1.257 1.15.864.525-.341.49-1.05.456-1.592-.007-.15.02.3 0 0" fill-rule="evenodd"></path></svg>Encoding...</div><div class="uppy-StatusBar-actions"><button type="button" class="uppy-u-reset uppy-StatusBar-actionCircleBtn" title="Cancel" aria-label="Cancel" data-uppy-super-focusable="true"><svg aria-hidden="true" focusable="false" class="uppy-c-icon" width="16" height="16" viewBox="0 0 16 16"><g fill="none" fill-rule="evenodd"><circle fill="#888" cx="8" cy="8" r="8"></circle><path fill="#FFF" d="M9.283 8l2.567 2.567-1.283 1.283L8 9.283 5.433 11.85 4.15 10.567 6.717 8 4.15 5.433 5.433 4.15 8 6.717l2.567-2.567 1.283 1.283z"></path></g></svg></button></div></div><div class="uppy uppy-Informer" aria-hidden="true"><p role="alert"> </p></div>';
                            progressindicators.innerHTML = html_to_insert;
                            $('.uppy-DashboardContent-back,.is-waiting,.uppy-Informer,.uppy-StatusBar-actions,.uppy-Dashboard-Item-action--remove').remove();
                            const updateFiles = files[fileKeys[0]];
                            const data = new FormData();
                            data.append("file", updateFiles.data);

                            axios.post(VIDEO_COMPRESS, data).then((response) => {
                                   const { responseData } = response.data;
                                   if (responseData && responseData.length > 0) {

                                          setTimeout(() => {
                                                 this.uppy.cancelAll()
                                                 this.setState({ backgroundModal: false, type: "video", height: responseData[0].height, width: responseData[0].width, })

                                          }, 300)
                                          if (updateData) { updateData({ ...responseData[0], type: "video" }) }
                                   }
                            }).catch((error) => {
                                   this.uppy.cancelAll()
                            })
                     }
                     catch (ex) {
                            this.uppy.cancelAll()
                     }
                     return false;
              }

       }

       componentDidMount() {
              var self = this;
              const { updateData } = self.props
              this.uppy.on('transloadit:result', (stepName, result) => {
                     self.props.onClickTouploadImage(`${TEMP_URL}${result.id}.${result.ext}`, () => {
                            self.props.UpdateType(result.type)
                            setTimeout(() => {
                                   this.uppy.cancelAll()
                                   this.setState({ backgroundModal: false, type: result.type })
                            }, 300)
                     })
                     if (updateData) updateData(result)
              })
              this.uppy.on('file-added', (file) => {
                     const isImage = file.type.startsWith('image/');
                     const isVideo = file.type.startsWith('video/');

                     if (isImage && file.size > 50000000) {
                            this.uppy.removeFile(file.id);
                            this.uppy.info(
                                   {
                                          message: 'File limit exceed!',
                                          details:
                                                 "File size shouldn't be more than 50 MB",
                                   },
                                   'error',
                                   5000,
                            );
                            // 100000000
                     } else if (isVideo && file.size > 100000000) { // 100 MB
                            this.uppy.removeFile(file.id);
                            this.uppy.info(
                                   {
                                          message: 'File limit exceed!',
                                          details:
                                                 "File size shouldn't be more than 100 MB",
                                   },
                                   'error',
                                   5000,
                            );
                     }
              });
       }

       onBackgroundClose = event => {
              this.uppy.cancelAll()
              this.setState({ backgroundModal: false })
       }

       onBackgroundOpen = event => {
              this.setState({ backgroundModal: true }, () => {
                     $(".uppy-Dashboard-close").click(() => {
                            this.uppy.cancelAll()
                            this.setState({ backgroundModal: false })
                     })
              })
       }

       updateProgressindicators = () => { $(".uppy-Dashboard-progressindicators").remove(); }

       remove_StatusBar = () => { $('.uppy-StatusBar').remove(); }

       onRemove = () => {
              this.uppy.cancelAll()
              this.setState({ type: '', thumbnail: '' })
              this.props.onClickTouploadImage("", () => { })
       }

       render() {
              const { imageStyle, backgroundImage, id, removeDelete, isEditPost, icon, thumbnail, isEdit, isTaggModal, isCover } = this.props
              const { backgroundModal, type, height, width } = this.state
              const content = window?.APP_CONTENT?.manual_upload ?? {};

              return <>
                     <div className='position-relative h-100 uploader_ mb-2'>
                            {backgroundImage ?
                                   <div className='w-100 bg-gray-100'>
                                          {type == "video" ? <VideoType mediaClass={'content_video w-100'} size={130} url={backgroundImage} poster={thumbnail} height={height} width={width} autoPlay={true} muted={true} isCover={isCover} controls={false} loop={true} />
                                                 :
                                                 <ImageType mediaClass={'position-relative overflow-hidden w-100'} size={130} height={height} width={width} url={backgroundImage} isCover={isCover} alt={""} />}
                                          {!isTaggModal ? !isEditPost ? <div className='f-center position-absolute bottom-0 start-0 w-100 p-2 upload_action_'>
                                                 {!isEdit ? <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">{content?.edit ?? ""}</Tooltip>}>
                                                        <Button id="product_edit" variant="icon" className="btn-active-primary mx-1 bg-white" onClick={() => this.setState({ backgroundModal: true })}>
                                                               <span className="svg-icon svg-icon-sm me-0">
                                                                      <FontAwesomeIcon type='solid' icon={'pen'} />
                                                               </span>
                                                        </Button>
                                                 </OverlayTrigger> : null}
                                                 {!removeDelete ? <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">{content?.remove ?? ""}</Tooltip>}>
                                                        <Button id="product_remove" variant="icon" className="btn-active-primary mx-1 rounded-circle bg-white" onClick={() => this.onRemove()}>
                                                               <FontAwesomeIcon icon={'xmark'} />
                                                        </Button>
                                                 </OverlayTrigger> : ''}
                                          </div> : '' : ''}
                                   </div>
                                   :
                                   <div className="f-center flex-column cursor-pointer border border-dashed border-2 p-3 rounded-3 h-100 text-center" onClick={() => this.setState({ backgroundModal: true })}>

                                          <span className='text-gray-300 mb-5' style={{ fontSize: 120, lineHeight: 1 }}>
                                                 {icon ?
                                                        <i className="fa-duotone fa-solid fa-image"></i>
                                                        :
                                                        <i className="fa-duotone fa-solid fa-bag-shopping-plus"></i>
                                                 }
                                          </span>
                                          <strong className="text-center text-muted mb-2">{content?.drag_and_drop ?? ""}</strong>
                                          <span className='text-muted fs-9'>{content?.media_size ?? ""}</span>
                                   </div>}
                            {
                                   backgroundModal ?
                                          <DashboardModal
                                                 animateOpenClose={true}
                                                 open={backgroundModal}
                                                 proudlyDisplayPoweredByUppy={false}
                                                 inline={false}
                                                 showProgressDetails={true}
                                                 height={750}
                                                 browserBackButtonClose={true}
                                                 showRemoveButtonAfterComplete={false}
                                                 uppy={this.uppy}
                                                 onRequestClose={this.onBackgroundClose}
                                                 closeAfterFinish={true}
                                                 plugins={['Url']}
                                          />
                                          : null
                            }

                     </div>


              </>
       }

}
